.section.form-w-text .container {
    background: $light_gray;
    padding: 25px 15px;
    font-family: 'Open Sans';
}
@media (min-width: $(desktop)) {
    .section.form-w-text .container {
        padding: 50px 0;
        margin-top: 50px;
    }
}
.section.form-w-text .container .text {
    margin: 0 auto !important;
}
@media (min-width: $(desktop)) {
    .section.form-w-text .container .text {
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        width: calc(99.99% * 10/12 - (30px - 30px * 10/12));
        margin: 0 auto !important;
    }
    .section.form-w-text .container .text:nth-child(1n) {
        margin-right: 30px;
        margin-left: 0;
    }
    .section.form-w-text .container .text:last-child {
        margin-right: 0;
    }
    .section.form-w-text .container .text:nth-child(12 '' ''n) {
        margin-right: 0;
        margin-left: auto;
    }
}
.section.form-w-text .container .text p {
    font-family: 'Open Sans';
}
.section.form-w-text .container .button {
    display: inline-block;
    clear: both;
    width: 200px;
    margin: 10px auto;
}
.section.form-w-text .container .form {
    display: none;
    width: 100%;
}
@media (min-width: $(desktop)) {
    .section.form-w-text .container .form {
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        width: calc(99.99% * 10/12 - (30px - 30px * 10/12));
        margin: 0 auto !important;
    }
    .section.form-w-text .container .form:nth-child(1n) {
        margin-right: 30px;
        margin-left: 0;
    }
    .section.form-w-text .container .form:last-child {
        margin-right: 0;
    }
    .section.form-w-text .container .form:nth-child(12 '' ''n) {
        margin-right: 0;
        margin-left: auto;
    }
    .section.form-w-text .container .form > div {
        margin-bottom: 0;
    }
}
.section.form-w-text .container h2 {
    font-size: 19px;
    color: $black;
}
@media (min-width: $(desktop)) {
    .section.form-w-text .container h2 {
        font-size: 24px;
    }
}
.section.form-w-text .container .description {
    color: $black;
}
.section.form-w-text .container .gform_wrapper {
    margin: 0;
}
.section.form-w-text .container .gform_wrapper div.validation_error {
    border: none;
}
.section.form-w-text .container .icon {
    float: left;
    margin-right: 15px;
    display: none;
}
@media (min-width: $(desktop)) {
    .section.form-w-text .container .icon {
        display: inline-block;
    }
}
.section.form-w-text .container .form {
    margin: 0 auto;
}
.section.form-w-text .container .form .gfield_error {
    background: none;
    border: none;
    margin: 0;
}
.section.form-w-text .container .form .gfield_error .ginput_container {
    margin-top: 0.375em;
}
@media (min-width: $(desktop)) {
    .section.form-w-text .container form ul {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
    }
}
.section.form-w-text .container form ul li {
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 auto;
        -ms-flex: 0 0 auto;
            flex: 0 0 auto;
    width: calc(99.99% * 12/12 - (30px - 30px * 12/12));
    margin: 0 auto;
}
.section.form-w-text .container form ul li:nth-child(1n) {
    margin-right: 30px;
    margin-left: 0;
}
.section.form-w-text .container form ul li:last-child {
    margin-right: 0;
}
.section.form-w-text .container form ul li:nth-child(1n) {
    margin-right: 0;
    margin-left: auto;
}
.section.form-w-text .container form ul li .ginput_container {
    background: $white;
    border-bottom: 3px solid $gray_medium2;
}
@media (min-width: $(desktop)) {
    .section.form-w-text .container form ul li:first-child,
                        .section.form-w-text .container form ul li:nth-child(2n) {
        -webkit-box-flex: 0;
