.section.form-w-text .container form ul label {
    font-size: 14px;
    position: relative;
    text-transform: none;
}
.section.form-w-text .container form ul input,
                .section.form-w-text .container form ul select {
    width: 100%;
    border: none;
    color: $gray_dark1;
    min-height: 40px;
    line-height: 28px;
    padding: 0.313em 0;
}
.section.form-w-text .container form ul .ginput_container_select {
    position: relative;
}
.section.form-w-text .container form ul .ginput_container_select::before,
                    .section.form-w-text .container form ul .ginput_container_select::after {
    display: none;
}
.section.form-w-text .container form ul .ginput_container_select select {
    padding: 5px;
    padding-right: 50px;
    height: 40px;
    -webkit-appearance: normal;
       -moz-appearance: normal;
            appearance: normal;
    position: relative;
    z-index: 3;
    box-sizing: border-box;
}
.section.form-w-text .container form ul .ginput_container_select::before {
    position: absolute;
    content: ' ';
    display: block;
    width: 0;
    height: 0;
    top: 17px;
    right: 14px;
    text-decoration: none;
    border-bottom: none;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-top: 7px solid $white;
    z-index: 2;
}
.section.form-w-text .container form ul .ginput_container_select::after {
    position: absolute;
    content: ' ';
    display: block;
    width: 40px;
    height: 100%;
    top: 0;
    right: 0;
    background: $gray_medium1;
    text-decoration: none;
    border: none;
    z-index: 1;
    margin: 0;
}
.section.form-w-text .container form input[type='submit'] {
    margin: 10px auto;
    display: block;
    width: 100% !important;
}
@media (min-width: $(desktop)) {
    .section.form-w-text .container form input[type='submit'] {
        width: auto !important;
        margin: 0 auto;
    }
}
